@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
  * {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    color: #3C4043;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

.particles {
    position:fixed;
    width: 100%; 
    height: 100%; 
    background-color: #f0f0f5; 
    background-repeat: no-repeat; 
    background-size: cover; 
    background-position: 50% 50%; 
    z-index: -1;
}

.background {
    position: fixed;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.05);
    width: 80vw;
    height: 100vh;
    z-index: -1;
}

@media (max-width: 767px) {
    .particles {
        display: none;
    }

    .background {
        display: none;
    }
}
.home-container {
    position: relative;
    width: 100%;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    justify-content: center;
    align-items: center;

}

.home-container .content-container {
    width: 85vw;
    min-height: 480px;
    height: 80vh;

    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.10);
    border-radius: 8px;
    padding: 96px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home-container .content-container .branding .profile {
    height: 11vw;
    width: 11vw;
    min-height: 185px;
    min-width: 185px;
    border-radius: 50%;
    transition: all 0.5s;
}

.home-container .content-container .branding .profile:hover {
    height: 17vw;
    width: 17vw;
}

.home-container .content-container .branding .title {
    font-size: 3.5vw;
    margin-top: 16px;
    color: #3C4043;
}

.home-container .content-container .branding .text {
    font-size: 1.4rem;
    color: #52565A;
}

.home-container .content-container .branding .contact-info-list {
    list-style-type: none;
    margin-top: 32px;
}

.home-container .content-container .branding .contact-info-list li {
    margin-top: 8px;
    font-size: 16px;
}

.home-container .content-container .branding .contact-info-icons {
    list-style-type: none;
    margin-top: 8px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 200px;
}

.home-container .content-container .branding .contact-info-icons li a .icon {
    transition: all 0.2s;
}

.home-container .content-container .branding .contact-info-icons li a .icon:hover {
    margin: -8px;
    height: 25px;
    width: 25px;
}

.home-container .content-container .navigation {
    position: relative;
    height: 100%;
}

.home-container .content-container .links {
    list-style: none;
    height: 100%;
    margin-left: 16px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.home-container .content-container .link {
    background-color: #3d55dd; /* azul antigo: #2c4af1 */
    color: white;
    border-radius: 8px;
    padding: 4vh 0;
    width: 32vw;
    font-weight: 500;
    text-decoration: none;
    font-size: 2rem;
    text-align: center;
    transition: opacity 0.2s;
}

.home-container .content-container .primeiro {
    margin-top: 45px;
    margin-bottom: 8px;
}

.home-container .content-container .ultimo {
    margin-top: 8px;
    margin-bottom: 45px;
}

.home-container .content-container .link:hover {
    opacity: 0.7;
}

.home-container .content-container .mobile {
    display: none;
    margin-top: 8px;
    margin-bottom: 45px;
}

.home-container .mobile-navigation {
    position: fixed;
    height: 25px;
    width: 30px;
    background: transparent;
    display: none;
    flex-direction: column;
    border: none;
    cursor: pointer;
    right: 32px;
    top: 32px;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0;
}

.home-container .mobile-navigation-line {
    width: 30px;
    padding: 0;
    margin: 0;
    height: 3px;
    border-radius: 2px;
    background-color: #3d55dd;
}

@media (max-width: 1023px) {
    .home-container .content-container .branding .contact-info-icons {
        width: 150px;
    }

    .content-container .navigation{
        margin-left: 18px;
    }

    .home-container .content-container .branding .profile {
        min-height: 150px;
        min-width: 150px;
    }

    .home-container .content-container .branding .title {
        margin-top: 8px;
    }

    .home-container .content-container .branding .contact-info-list {
        list-style-type: none;
        margin-top: 22px;
    }

    .home-container .content-container .ultimo {
        display: none;
    }

    .home-container .content-container .mobile {
        display: block;
    }
}

@media (max-width: 767px) {
    .home-container .navigation {
        display: none;
    }

    .home-container .mobile-navigation {
        display: flex;
    }

    .home-container .content-container {
        width: 100%;
        height: 100vh;
    
        background: none;
        box-shadow: none;
        padding: 32px;
    
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .branding-logo {
        display: flex;
        flex-direction: row;
    }

    .profile {
        width: 10px;
        min-width: 10px;
        height: 10px;
        min-height: 10px;
    }
    
    .home-container .content-container .branding .title {
        font-size: 6vw;
        margin-top: 20px;
    }

    .home-container .content-container .branding .text {
        font-size: 1.1rem;
    }

    .home-container .content-container .branding .profile {
        height: 18vw;
        width: 18vw;
        min-height: 80px;
        min-width: 80px;
        margin: 8px;
    }
}
.side-drawer {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 70%;
    max-width: 400px;
    background-color: #f0f0f5;
    box-shadow: 0px 0px 100px rgba(0, 0, 0, 0.1);
    z-index: 15;
    transform: translateX(120%);
    transition: transform 0.4s ease-out;
}

.side-drawer.open {
    transform: translateX(0);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 32px;
}

.side-drawer li {
    margin: 0.8rem 0;
    transition: letter-spacing 0.4s;
}

.side-drawer a {
    color: #3d55dd;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 500;
}

.side-drawer li:hover, .side-drawer li:active {
    letter-spacing: 2px;
}

.side-drawer-links-list .icon {
    font-size: 1rem;
}

@media (min-width: 1024px) {
    .side-drawer {
        display:none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 14;
    top: 0;
    left: 0;
    -webkit-animation: load 0.5s 1 forwards;
            animation: load 0.5s 1 forwards;
}


@media (min-width: 1024px) {
    .backdrop {
        display:none;
    }
}

@-webkit-keyframes load {
    from {
        opacity: 0;
        background: #f0f0f5;
    }
    to {
        opacity: 0.7;
        background: #f0f0f5;
    }
}

@keyframes load {
    from {
        opacity: 0;
        background: #f0f0f5;
    }
    to {
        opacity: 0.7;
        background: #f0f0f5;
    }
}
.projects-container {
    display: flex;
    justify-content: center;
}

.projects-content-container {
    padding: 32px 64px;
    width: 80vw;
    height: 100vh;
}

.project-tiles {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

/* Laptop L DOWN */
@media (max-width: 1439px) {
    .project-tiles {
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 1023px) {
    .project-tiles {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 767px) {
    .projects-content-container {
        width: 100vw;
        padding: 32px;
        padding-top: 200px;
    }
}
.toggle-button {
    margin-left: auto;
    height: 25px;
    width: 30px;
    background: transparent;
    display: flex;
    flex-direction: column;
    border: none;
    cursor: pointer;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    padding: 0;
    margin: 0;
    height: 3px;
    border-radius: 2px;
    background-color: #3d55dd;
}

@media (min-width: 1024px) {
    .toggle-button {
        display:none;
    }
}

@media (max-width: 767px) {
    .toggle-button {
        position: relative;
        right: 32px;
        top: 0px;
    }
}
.navbar-container {
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    background: #f0f0f5;
    top: 0;
    width: 80vw;
    margin-left: -64px;
    margin-top: -32px;
    margin-bottom: 32px;
    padding: 0 64px;
}

.navbar-container .branding .icon{
    width: 1.4vw;
    height: 1.4vw;
    margin-right: 16px;
    transition: all 0.2s;
    min-width: 18px;
    min-height: 18px;
}

.navbar-container .branding .icon:hover {
    margin-top: -10px;
    width: 1.7vw;
    height: 1.7vw;
}

.navbar .link .icon {
    width: 1.3vw;
    height: 1.3vw;
}

.navbar-container .logo {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 8px;
    margin-top: 32px;
}

.navbar-container h1 {
    margin-top: 32px;
    font-size: 2.2vw;
}

.navbar {
    position: absolute;
    right: 64px;
    width: 60%;
    height: 100%;

    display: flex;
    justify-content: space-around;
}

.navbar .link {
    color: #3d55dd;
    border-radius: 8px;
    font-weight: bold;
    text-decoration: none;
    font-size: 1.5vw;
    text-align: center;
    transition: all 0.2s;
    margin-top: 42px;
}

.navbar .link:hover {
    opacity: 0.6;
}
/* Laptop L DOWN */
@media (max-width: 1439px) {
    .navbar {
        width: 50%;
    }

    .navbar-container h1 {
        font-size: 3vw;
    }

    .navbar .link {
        font-size: 1.8vw;
    }
}

@media (max-width: 1023px) {
    .navbar {
        display: none;
    }
}

@media (max-width: 767px) {
    .navbar-container {
        position: fixed;
        width: 105%;
        margin: 0;
        margin-left: -40px;

        padding: 32px;
        padding-top: 18px;
    }

    .navbar-container .logo {
        margin-top: 10px;
        margin-left: 0px;
    }
    
    .navbar-container h1 {
        font-size: 20px;
        margin-top: 10px;
    }

    .navbar-container .branding .icon{
        margin-right: 8px;
    }
}
.project-tile {
    background: #f0f0f5;
    padding: 32px;
    box-shadow: 0 0 100px -40px rgba(0, 0, 0, 0.15);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    margin: 16px;
}

.center {
    display: flex;
    text-align: center;
}

.project-tile .img {
    width: 100%;
    height: 20vh;
}

.project-tile h1 {
    font-size: 1.3vw;
    margin: 4px 0;
    margin-top: 8px;
}

.project-tile p {
    font-size: 1rem;
    text-align: justify;
    margin: 4px 0;
}

.project-tile .links {
    width: 100%;

    display: flex;
    justify-content: space-between;
}

.project-tile a {
    margin: 8px;
    margin-bottom: 0;
    text-decoration: none;
    color: #2c4af1;
    font-weight: bold;
    font-size: 19px;
    transition: all 0.2s;
}

.project-tile a:hover {
    opacity: 0.6;
}

@media (max-width: 1439px) {
    .project-tile {
        margin: 12px;
    }

    .project-tile .img {
        height: 18vh;
    }

    .project-tile h1 {
        font-size: 20px;
    }
}

@media (max-width: 767px) {
    .project-tile {
        margin-right: 0px;
        margin-left: 0px;
    }
}
.sobre-container {
    display: flex;
    justify-content: center;
}

.sobre-content-container {
    padding: 32px 64px;
    width: 80vw;
    height: 100vh;
}

.sobre-content-container .text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50vw;
    margin: 0 auto;
}

.sobre-content-container .text-container h1 {
    margin-top: 32px;
    margin-bottom: 16px;
    width: 100%;
    font-size: 3rem;
}

.sobre-content-container .sobre-img {
    margin: 32px 0;
    width: 100%;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
}

.sobre-content-container .text-container p {
    width: 100%;
    font-size: 1.2rem;
    text-align: justify;
}

.sobre-content-container .text-container .last {
    padding-bottom: 32px;
}

@media (max-width: 1023px) {
    .sobre-content-container .text-container {
        width: 100%;
    }
    
}

@media (max-width: 767px) {
    .sobre-content-container {
        width: 100vw;
        padding: 32px;
        padding-top: 200px;
    }
}
.curriculo-container {
    display: flex;
    justify-content: center;
}

.curriculo-content-container {
    padding: 32px 64px;
    width: 80vw;
    height: 100vh;
}

.curriculo-container .curriculo-content-container .pdf {
    padding: 16px;
    width: 100%;
    height: 70vh;
}

.curriculo-container .curriculo-content-container .download {
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 700;
    margin: 16px;
    color: #3d55dd;
    transition: opacity 0.2s;
}

.curriculo-container .curriculo-content-container .download:hover {
    opacity: 0.7;
}

.curriculo-container .curriculo-content-container .mobile {
    display: none;
    position: absolute;
    top: 50vh;
    left: 10vw;
}

@media (max-width: 767px) {
    .curriculo-content-container {
        width: 100vw;
        padding: 32px;
        padding-top: 200px;
    }

    .curriculo-container .curriculo-content-container .mobile {
        display: inline-block;
    }
}
